
* {
  margin: 0;
  padding: 0;
}

html {
    overflow: -moz-scrollbars-vertical; 
    overflow-y: scroll;
}

.news-post{
  width:100%;
  min-width:300px;
  max-width:900px;
  /*height:200px;*/

  border-style: solid none none none;
  border-width:1px;
  border-color:#aba9a9;
  font-size:18px;
  display:flex;
  justify-content: center;
  align-items: center;
  margin:0 auto;
  padding-bottom: 30px;
  padding-top:15px;
}
.news-flex{
  display: flex;
  flex-direction: row;
}

.newsContent{
  margin-left:10px;
  margin-top:0;

}

.newsTitle{
  font-weight:700;
  margin-left:0px;
  margin-top:0;
}

.newsDescription{
  /*height: 3em;*/
  overflow: hidden;
   text-overflow: ellipsis;
   display: -webkit-box;
   -webkit-box-orient: vertical;
   -webkit-line-clamp: 2; /* number of lines to show */
   line-height:  1;        /* fallback */
   max-height: 2* 1;       /* fallback */
}

.newsContent{
  margin-left:10px;
  /*overflow:hidden;*/
}

.imageContainer{
  height:inherit;
  display: flex;
  justify-content: center;
  align-items: center;
}
.new-image{
  width:200px;
  aspect-ratio:3/2;
  margin:auto;
  object-fit:cover;
  border-radius:5%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.mainContent{
  display:flex;
  flex-direction:row;
  margin:0;
}

.navbar{
  flex: 0 0 auto;
  background-color:white;
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  display:flex;
  width:300px;
  height:300px;
  /*justify-content:center;*/
  padding-top:100px;
  flex-direction:column;

}

/*.options{
  height:80vh;
}*/

.newsDisplay{
  max-width:calc(95% - 300px);
  display:flex;
  flex-direction:column;
  flex: 1 0 auto;
}

.option{
  padding:10px;
  border:solid 1px #ccc;
  font-size:20px;
  font-weight:600;
  width:70%;
  height:18px;
  margin:0 auto;
  position:relative;


}

.newsContainer{
  display:flex;
  justify-content:center;
  flex-direction:column;
}

.currentCategory{
  position: -webkit-sticky; /* Safari */
  position: sticky;
  top: 0;
  text-align:center;
  padding-top:60px;
  font-size:30px;
  background-color:white;
  /*border-style: solid none none none;*/
  border-width:1px;
  font-family: "Times New Roman", Times, serif;
  font-weight:500;
  text-decoration-line: underline;
  color:#5e5e5e;

}

.categoryTitle{
  text-align:center;
  font-size:20px;
  padding:10px;
  font-weight:600;
  color:#9d9d9d;
}

.logoContainer{
  display:flex;
  justify-content:center;
  width:100%;
}

.logoImage{
  width:50%;
}

.loadingIconContainer{
  background-color:white;
  width:100%;
  height:200px;
  display:flex;
  justify-content:center;
  align-items:center;
}

.loadingIcon{
  width:100px;
  height:100px;
}
.active-category{
  width:60px;
  position:absolute;
  right:-35px;
  display:flex;
  background-color:transparent;
}
.search-icon{
  float:right;
}

.delete-icon{
  margin-left: 10px;
}
.optionTitle{
  width:100%;
  height:100%;
  position:absolute;
  left:1;

}
.readMore{
  width:100%;
  text-align:center;
}

.delete-icon:hover {
  background-color: yellow;
}
@media only screen and (max-width: 600px) {
  /* For phone: */

}

@media only screen and (max-width: 900px)  {
  /* For tablets: */
  .mainContent{
    flex-direction:column;

  }
  .newsDisplay{
    width:100%;
    flex: none;
    max-width:100%;

  }
  .navbar{
    flex: none;
    width:100%;
    position:static;
  }
  .currentCategory{
    /*display:none;*/
  }
  .logoImage{
    width:200px;
  }

  .news-post{
    width:90%;
  }
  .currentCategory{
    padding-top:10px;
  }
  .navbar{
    padding-top:60px;
  }
}
